.regWrap {
    background-color:white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    height: 100%;
    padding: 0rem .43rem;
    font-family: 'PingFang-SC-Medium';
}
.payWrap {
    -ms-flex-align: stretch;
        align-items: stretch;
}
.reg {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    margin-top: 1.26rem;
    width: 100%;
}
.wrap {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    padding-top: .53rem;
    padding-bottom: .37rem;
    border-bottom: 1px solid #DDDDDD;
    -ms-flex-align: center;
        align-items: center;
}
.wrap span {
    width: 2.4rem;
}
.reg input {
    background-color: transparent;
    width: 80%;
    color: #D6D6D6;
}
.regWrap p {
    color: #D6D6D6;
}
.regWrap .tao {
    margin-top: 2.08rem;
    width: 2.67rem;
    height: 2.67rem;
}
.reg .input {
    border: 0px;
    /* border-bottom: 1px solid white;
    border-color: #D6D6D6; */
    -ms-flex:1 1;
        flex:1 1;
}
.reg select {
    -ms-flex:1 1;
        flex:1 1;
    /*Chrome和Firefox里面的边框是不一样的，所以复写了一下*/
    border: 0;
    /*很关键：将默认的select选择框样式清除*/
    appearance:none;
    -moz-appearance:none;
    -webkit-appearance:none;
    /*将背景改为白色*/
    background:white;
    color: #666666;
    /*加padding防止文字覆盖*/
    padding-right: 14px;
}
/*清除ie的默认选择框样式清除，隐藏下拉箭头*/
.reg select::-ms-expand { 
    display: none; 
}
.reg select option {

}

.input::-webkit-input-placeholder {
    /* WebKit browsers*/
    color: #D6D6D6;
    font-size: 14px;
    text-align: left;
}
.input:-moz-placeholder {
    /* Mozilla Firefox 4 to 18*/
    color: #D6D6D6;
    font-size: 14px;
    text-align: left;
}
.input::-moz-placeholder {
    /* Mozilla Firefox 19+*/
    color: #D6D6D6;
    font-size: 14px;
    text-align: left;
}
.reg .input:-ms-input-placeholder {
    /* Internet Explorer 10+*/
    color: #D6D6D6;
    font-size: 14px;
    text-align: left;
}
.reg .button {
    color: #548EEF;;
    margin-top: .62rem;
    margin-bottom: .50rem;
    width: 100%;
    height: 1.07rem;
    background-color: white;
    border: 1px solid #548EEF;
}
.reg .interactive:active {
    background-color: rgb(243, 196, 103);
    color: white;
  }
.reg div {
    width: 100%;
}
.regWrap h2 {
    margin-top: 2.4rem;
}
.text-right {
    width:100%;
    margin-top: .11rem;
    text-align: left;
}
.needBorder {
    color: #D6D6D6;
    /* border: 1px solid #D6D6D6; */
    padding: 0rem .24rem;
    border-radius: .26rem;
}
.captchaPosit {
    position: absolute;
    top: .52rem;
    right: .27rem;
}
.verification {
    color: #548EEF;
    border: 1px solid #548EEF;
}
.right35 {
    right: 1.5rem;
    color: transparent;
    border-color: transparent;
}
.defaultColor {
    color: #548EEF;
}
.wrap {
    position: relative;
}
.goLogin {
    text-align: right;
    width: 65%;
    text-decoration: underline;
}
.goLogin a {
    color: #D6D6D6;
}
.Loading {
    position: absolute;
    top: 5.2rem;
}
.marB4 {
    top: 0.6rem;
}
.tri {
    width: .32rem;
    height: .27rem;
}
.payWay {
    margin-top: 1.33rem;
}
.payTip {
    background-color: #EEF4FE;
    padding: .43rem .27rem;
    margin-top: .43rem;
}
.count {
    margin-bottom: .43rem;
}
.payTip input {
    border: 0;
    background-color: #EEF4FE;
    width: 2.67rem;
}
.payTip span {
    color: #548EEF;
}
.payWrap .toast {
    position: absolute;
    top: 5.33rem;
    left: 4rem;
}




